.footer {
    width: 100%;
    height: 100vh;
    background-color: #673AB7;
    color: #FFFFFF;
    padding-top:  100px;
    position:fixed;
    z-index: -2;
    bottom: 0;
    left: 0;
}

.footer-info {
    font-size: 35px;
    padding: 30px 0 30px 30px;
    width: 90%;
    margin: 0 auto;
}

.footer-info > h1 {
    padding-bottom: 20px;
}

.footer-contact {
    font-size: 22px;
    padding: 100px 0 30px 30px;
    width: 90%;
    margin: 0 auto;
}

.footer-sns {
    width: 93%;
    position: absolute;
    bottom: 20px;
    left: 0;
    right: 0;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: center;
}

.sns-link {
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr 1fr;
}

.linkedin,
.twitter,
.facebook {
    color: white;
    font-size: 35px;
    margin: 0 auto;
    padding-bottom: 10px;
    border-bottom: 4px solid transparent;  
}

.linkedin:hover,
.twitter:hover,
.facebook:hover {
    border-color: white;
    transition: all 0.4s ease-out;
}

.design-by {
    font-size: 18px;
    align-items: flex-start;
    padding-left: 50px;
    color: white;
}

@media only screen and (max-width: 960px) {

    .footer-info {
        text-align: center;
        padding: 0;
        font-size: 20px;
    }

    .footer-contact {
        font-size: 16px;
        padding: 100px 0 30px 0;
        text-align: center;
    }

    .footer-sns {
        grid-template-columns: 1fr;
        text-align: center;
    }

    .design-by {
        padding: 0 0 40px 0;
    }
}